import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';

@Component({
  selector: 'app-clinic-login',
  templateUrl: './clinic-login.component.html',
  styleUrls: ['./clinic-login.component.css']
})
export class ClinicLoginComponent implements OnInit {

  Rolebox;
  AvalaibleRoles;
  constructor(private router: Router) { }

  ngOnInit() {
    this.AvalaibleRoles = ['Supervisor' , 'Doctor', 'FrontDest'];
    this.Rolebox = 'Role';
  }
  UpdateRole(Role) {
    this.Rolebox = Role;
  }
  RouterFunction(){
    this.router.navigate(['SupervisorPanel']);
  }

}
